import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { ContactComponent } from './components/contact/contact.component';
import { AboutComponent } from './components/about/about.component';
import { ProjectsComponent } from './components/projects/projects.component';
import { NumberGameComponent} from './components/projects/number-game/number-game.component';
import { RainbowBreakoutComponent } from './components/projects/rainbow-breakout/rainbow-breakout.component';
import { SpaceInvadersComponent } from './components/projects/space-invaders/space-invaders.component';

const routes: Routes = [
{
path: '',
 pathMatch: 'full',
component: HomeComponent
},

{
path: 'contact',
component: ContactComponent
},
{
path: 'projects',
component: ProjectsComponent
},
{
path: 'about',
component: AboutComponent
},
{
path: 'numberGame',
component: NumberGameComponent
},
{
path: 'rainbowBreakout',
component: RainbowBreakoutComponent
},
{
path: 'spaceInvader',
component: SpaceInvadersComponent
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
