import { Component, OnInit } from '@angular/core';
declare const draw: any;


@Component({
  selector: 'app-rainbow-breakout',
  templateUrl: './rainbow-breakout.component.html',
  styleUrls: ['./rainbow-breakout.component.scss']
})
export class RainbowBreakoutComponent implements OnInit {

 onClick() {
    draw();
  }

  constructor() {
    this.loadScripts();
  }

  ngOnInit() {
    
            }
  loadScripts() {
    const externalScriptArray = [
      '../assets/js/breakoutGame.js'
    ];
    for (let i = 0; i < externalScriptArray.length; i++) {
      const scriptTag = document.createElement('script');
      scriptTag.src = externalScriptArray[i];
      scriptTag.type = 'text/javascript';
      scriptTag.async = false;
      scriptTag.charset = 'utf-8';
      document.getElementsByTagName('head')[0].appendChild(scriptTag);
    }
  }
}